import React, { useState } from "react";
import user from '../../image/avtorization/user.svg';
import email from '../../image/avtorization/mail.svg';
import password from '../../image/avtorization/password.svg';
import styles from './PageRegistration.module.scss';
import { useForm, SubmitHandler } from 'react-hook-form';
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import {InputsRegistrationProps} from '../../interface/InputsRegistrationProps/InputsRegistrationProps'
import { useAppDispatch } from "../../redux/hooks/hooks";
import { addUser, ageUser, cityUser, univerUser } from "../../redux/features/User/userSlice";
import { Navigate } from "react-router-dom";

const schema = yup.object({
    password: yup.string().required().min(6,''),
    confirmPassword: yup.string().required().oneOf([yup.ref('password')], '  '),
mail:yup.string().required().email()
  }).required();

function PageRegistration (){
   const dispatch=useAppDispatch();
   
    const { register, formState:{errors}, handleSubmit } = useForm<InputsRegistrationProps>({resolver: yupResolver(schema)});
    const [data, setData]=useState(false)
    const onSubmit: SubmitHandler<InputsRegistrationProps> = data =>{
        console.log(data); 
        const { login, age, city, univer} =data;
        dispatch (addUser(login));
        dispatch (ageUser(age));
        dispatch (cityUser(city));
        dispatch(univerUser(univer));
        setData(data=>!data)    
    }

      
    return(
      <section className={styles.registration__section}>
<form className={styles.registration__form} onSubmit={handleSubmit(onSubmit)}>
    <p></p>
 
     
        <label >
             :
            <input type='text' placeholder='' {...register('login',{required:true, minLength:5})}/>
        <img src={user} alt=' ' className={styles.registration__user}/>
        </label >
        <label >
         : 
      <input type='email' placeholder=' '  className={styles.otherEmail__input} {...register('mail',{required:true, pattern:/^.+\@.+\..+$/})} 
      style={{border:errors.mail&&`1px solid #EB5757`}}/>
      <img src={email} alt=' ' className={styles.registration__mail}/>
      {errors.mail&&<p className={styles.errors__p}>   !</p>}
      </label>
      <label> :
        <input type='number' placeholder='' {...register('age', {min:14, max:99})} />
      </label>
      <label> :
        <input type='text' placeholder='' {...register('city', {maxLength:15})} />
      </label>
      <label> :
        <input type='text' placeholder='' {...register('univer', {maxLength:35})} />
      </label>
      <label >
             :
      <input type='password' placeholder='' {...register('password',{required:true, minLength:6})} />
      <img src={password} alt=' ' className={styles.registration__password}/>
      </label>
      <label >
             :
      <input type='password' placeholder=' ' {...register('confirmPassword',{required:true, minLength:6})}
      style={{border:errors.confirmPassword&&`1px solid #EB5757`}} />
      <img src={password} alt=' ' className={styles.registration__passwordtwo}/>
     {errors.confirmPassword&&<p className={styles.errors__p}>  !</p>}
       </label>
      <button className={styles.registration__btn} type='submit'></button>
      {data&&<Navigate replace to='/ ' />}
        
</form>
       
</section>
    )
}
export default PageRegistration;